/* ─────────────────────────  GLOBAL  ───────────────────────── */
.experience-container{
  max-width:1000px;margin:0 auto;padding:2rem;
}
.section-title{
  font-size:2rem;margin-bottom:2rem;
  background:linear-gradient(135deg,#6e8efb,#a777e3);
  -webkit-background-clip:text;background-clip:text;color:transparent;
  display:flex;align-items:baseline;gap:1rem;
}
.experience-tenure{font-size:1rem;color:#6b7280;font-weight:400}

/* ───────────────────────  TIMELINE SPINE  ─────────────────── */
.timeline-visualization{position:relative;margin:3rem 0}
.timeline-visualization::before{
  content:"";position:absolute;top:0;left:11px;height:100%;width:2px;
  background:linear-gradient(#6e8efb,#a777e3);z-index:0;
}

/* Header Years */
.timeline-header{
  display:flex;justify-content:space-between;position:relative;
  margin-bottom:3rem;padding:0 2rem;
}
.timeline-header::before{
  content:"";position:absolute;top:50%;left:2rem;right:2rem;height:2px;
  background:#e5e7eb;z-index:0;
}
.timeline-year{
  background:#fff;padding:0 .5rem;font-weight:500;color:#6b7280;
  position:relative;z-index:1;
}

/* ─────────────────  CAREER-BREAK BAR & LABEL  ─────────────── */
.timeline-break-range{
  position:absolute;top:25px;height:14px;
  background:linear-gradient(90deg,#6366f1 0%,#a855f7 50%,#6366f1 100%);
  background-size:200% 100%;animation:glowPulse 4s ease-in-out infinite;
  border-radius:10px;box-shadow:0 0 8px 2px rgba(99,102,241,.35);
  left:var(--break-left,12.5%);width:var(--break-width,37.5%);z-index:2;
}
@keyframes glowPulse{
  0%,100%{background-position:0 0;box-shadow:0 0 8px 2px rgba(99,102,241,.35)}
  50%{background-position:100% 0;box-shadow:0 0 14px 4px rgba(168,85,247,.45)}
}
.timeline-break-label{
  position:absolute;top:115%;left:50%;transform:translateX(-50%);
  padding:.25rem .8rem;border-radius:12px;background:#fff;
  color:#7e22ce;font-size:.75rem;font-weight:600;white-space:nowrap;
  box-shadow:0 2px 6px rgba(0,0,0,.2);
}
.timeline-break-label::before{content:"⏸";margin-right:.25rem;color:#a777e3}
.timeline-break-overlay{margin-bottom:5rem;position:relative}

/* ─────────────────────  COMPANY / ROLES  ─────────────────── */
.company-track{position:relative;margin-bottom:2.5rem;padding-left:180px}
.company-header{
  position:absolute;left:0;width:160px;display:flex;align-items:center;gap:.8rem;
}
.company-logo{width:30px;height:30px;object-fit:contain;border-radius:4px}
.company-header h3{font-size:.95rem;color:#4b5563;font-weight:600;margin:0}

/* Role card */
.role-block{
  position:relative;margin:1rem 0;padding:1.2rem;background:#fff;border-radius:8px;
  box-shadow:0 2px 8px rgba(0,0,0,.08);border-left:3px solid #a777e3;
}
.role-block.current{border-left-color:#6e8efb;background:#f8f9fe}
.role-marker{
  position:absolute;left:-8px;top:50%;transform:translateY(-50%);
  width:16px;height:16px;border-radius:50%;background:#fff;border:3px solid #a777e3;
}
.role-block.current .role-marker{border-color:#6e8efb;box-shadow:0 0 0 3px rgba(110,142,251,.2)}

.role-content h4{margin:0 0 .3rem;color:#111827;font-size:1.1rem}
.role-meta{
  display:flex;gap:1rem;font-size:.85rem;color:#6b7280;margin-bottom:.5rem;
  flex-wrap:wrap;
}
.role-content p{margin:.5rem 0 0;color:#4b5563;font-size:.95rem;line-height:1.5}

/* ─────────────────────  CAREER-BREAK CARD  ───────────────── */
.career-break{
  position:relative;margin:2rem 0;padding:1.5rem;
  background:linear-gradient(#f9f0ff,#f3e8ff);border-radius:12px;
  border-left:4px solid #a777e3;
}
.break-marker{
  position:absolute;left:-8px;top:50%;transform:translateY(-50%);
  width:16px;height:16px;border-radius:50%;background:#fff;border:3px dashed #d1d5db;
}
.break-content{padding-left:1.5rem}
.break-content h4{
  color:#7e22ce;font-size:1.2rem;margin-bottom:.5rem;display:flex;align-items:center;gap:.5rem;
}
.break-content h4::before{content:"🎓"}
.break-content .role-meta{color:#8b5cf6}
.break-content p{color:#7c3aed;font-weight:500}

/* ─────────────────────  DOMAIN EXPERTISE  ────────────────── */
.domain-expertise{
  display:grid;grid-template-columns:repeat(auto-fit,minmax(280px,1fr));
  gap:1.5rem;margin-top:2rem;
}
.domain-card{
  background:#fff;border-radius:8px;padding:1.5rem;
  box-shadow:0 2px 10px rgba(0,0,0,.05);border-top:3px solid #a777e3;
  transition:transform .3s,box-shadow .3s;
}
.domain-card:hover{transform:translateY(-5px);box-shadow:0 5px 15px rgba(0,0,0,.1)}
.domain-card h3{font-size:1.2rem;margin-bottom:.5rem;color:#111827}
.domain-card .duration{display:block;font-size:.85rem;color:#6b7280;margin-bottom:.8rem}
.domain-card p{font-size:.95rem;color:#4b5563;line-height:1.6}

/* ─────────────────────  RESPONSIVE (<768px)  ─────────────── */
@media(max-width:768px){
  .experience-container{padding:1.5rem}
  .company-track{padding-left:0}
  .company-header{position:static;margin-bottom:1rem}
  .timeline-header{padding:0 1rem}
  .timeline-header::before{left:1rem;right:1rem}

  /* narrower break bar override */
  .timeline-break-range{left:25%!important;width:25%!important}
  .timeline-break-label{font-size:.8rem;padding:3px 8px;line-height:1.2;width:120px;top:-40px}

  .domain-expertise{grid-template-columns:1fr}
  .role-meta{flex-direction:column;gap:.3rem}
  .role-content h4{font-size:1rem}
}
