/* src/styles/About.css */

.about-outer-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2.5rem 1.5rem;
  /* box-sizing: border-box; */
}

.about-container {
  max-width: 900px;
  text-align: left;
}

.section-title {
  font-size: 2.2rem;
  margin-bottom: 1.8rem;
  background: linear-gradient(135deg, #6e8efb, #a777e3);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent !important; /* Forces the gradient to show */
}

.about-description {
  font-size: 1.05rem;
  line-height: 1.8;
  margin-bottom: 1.5rem;
  color: #4b5563; /* Soft gray */
}

.highlight-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.8rem;
  margin-top: 2.5rem;
}

.card {
  background: white;
  border-radius: 12px;
  padding: 1.8rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  border: 1px solid rgba(110, 142, 251, 0.15);
  position: relative;
  overflow: hidden;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(110, 142, 251, 0.1);
}

.card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(to bottom, #6e8efb, #a777e3);
}

.card h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #111827; /* Dark gray */
  padding-left: 0.5rem;
}

.card p {
  font-size: 0.95rem;
  color: #6b7280; /* Secondary text */
  line-height: 1.6;
  padding-left: 0.5rem;
}

/* Emoji spacing */
.card h3::before {
  margin-right: 0.5rem;
}
