.sidebar {
  width: 280px;
  height: 100vh;
  background: white;
  color: var(--text-primary);
  padding: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow-y: auto;
  border-right: 1px solid rgba(0,0,0,0.05);
}

/* Profile Image */
.profile-pic {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid transparent;
  background: var(--gradient-primary) border-box;
  margin-bottom: 1.5rem;
  transition: all 0.3s;
}

.profile-pic:hover {
  transform: scale(1.08);
  box-shadow: 0 0 20px rgba(110, 142, 251, 0.3);
}

/* Text & Typography */
h1 {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 24px;
  text-align: left;
  line-height: 1.3;
  color: var(--text-primary);
}

.title-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-bottom: 20px;
  flex-wrap: nowrap;
}

.role-pill {
  background: var(--gradient-primary);
  color: white;
  border-radius: 20px;
  padding: 4px 12px;
  font-size: 0.7rem;
  font-weight: 500;
  white-space: nowrap;
}

.subtitle {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-align: center;
  margin-bottom: 32px;
  line-height: 1.5;
}

/* Contact Info */
.contact-info {
  width: 100%;
  font-size: 0.95rem;
  line-height: 1.8;
}

.contact-item,
.contact-link {
  color: var(--text-secondary);
  transition: all 0.3s;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 0.8rem;
  font-size: 0.95rem;
}

.contact-link:hover {
  color: #6e8efb;
}

/* Social Icons */
.social-footer {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 1rem;
}

.social-icon {
  color: var(--text-secondary);
  transition: all 0.3s;
  font-size: 1.5rem;
}

.social-icon:hover {
  color: #6e8efb;
  transform: scale(1.3);
}
